<template>
  <div class="w-full max-w-screen mx-auto">
    <div class="mt-8 bg-level-1-light opacity-95 shadow-lg overflow-hidden rounded-lg">
      <section aria-labelledby="asset-title">
        <div class="rounded-lg border-gradient-br-l2-light-purple-three-level-2-dark border-transparent border-solid border-4 overflow-hidden shadow">
          <div class="mt-4 mx-8 font-lg text-right">amount : {{ submissions.length }}</div>
          <div class="flex flex-col items-center justify-center mb-8 mt-4">
            <Button v-if="submissions.length > 0" label="Load Next Property" customClasses="" @click="openProperty"/>
            <Button v-if="submissions.length <= 0" label="Nothing to review yet" customClasses="" />
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Button from '../views/common/Button.vue'

export default {
  name: 'Assets',
  components: {
    Button,
  },
  computed: {
    ...mapGetters({
      submissions: 'submissions'
    }),
  },
  methods: { 
    ...mapActions({
      syncWallet: 'syncWallet',
      syncSubmissions: 'syncSubmissions'
    }),
    openProperty() {     
      this.$router.push(`/submission/${this.submissions.length-1}`)
    }
  },
  created() {
    this.syncWallet()
    this.syncSubmissions()   
  },
}
</script>