import { createRouter, createWebHashHistory } from 'vue-router'
import Assets from '@/components/sections/Assets.vue'
import Submission from '@/components/sections/Submission.vue'

export default new createRouter({
  history: createWebHashHistory(),
  routes: [
    {
      path: '/',
      redirect: '/assets'
    },
    {
      path: '/assets',
      alias: '/',
      name: 'assets',
      component: Assets
    },
    {
      path: '/submission/:id',
      alias: '/',
      name: 'submission',
      component: Submission
    },
  ]
})