<template>
  <div>
    <button class="m-4"  @click="backToAsset">Back</button>
    <submission-item v-if="submission" :submission="submission" :loadNext="loadNext"/>
  </div>
</template>
<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import SubmissionItem from '../views/submissions/SubmissionItem.vue'

export default {
  name: 'Submission',
  components: {
    SubmissionItem,
  },
  computed: {
    ...mapGetters({
      submissionMap: 'submissionByIndex',
      submissions: 'submissions'
    }),
    submission() {
      return this.submissionMap.get(parseInt(this.$route.params.id))
    }
  },
  methods: { 
    ...mapActions({
      syncWallet: 'syncWallet',
      syncSubmissions: 'syncSubmissions'
    }),
    ...mapMutations({
      removeSubmissionByIndex: 'removeSubmissionByIndex'
    }),
    loadNext() {
      if (parseInt(this.$route.params.id) > 0) {
        this.removeSubmissionByIndex(parseInt(this.$route.params.id))
        this.$router.push(`/submission/${parseInt(this.$route.params.id)-1}`)
      }
      else this.$router.push('/assets')
    },
    backToAsset() {
      this.$router.push('/assets')
    }
  },
  created() {
    this.syncWallet()
    this.syncSubmissions()
  },
}
</script>
