import EthereumClient from '../ethereum/ethereumClient'
import PlatfromABI from './abi/Platform.json'

const contractAddress = "0x0f173d9E444Bc4C3443D558dE848A369e8763077" // TODO: ADD CONTRACT ADDRESS
const contractAbi = PlatfromABI
const startBlock = 0 // TODO: ADD CONTRACT DEPLOYMENT BLOCK TO AVOID QUERYING EXTRA HISTORY

/**
 * Platform contract
 * @param {EthereumClient} ethereumClient Ethereum client
 */
class PlatformContract {
  constructor(
    ethereumClient
  ) {
    this.contract = ethereumClient.getContract(contractAddress, contractAbi)
    this.mutableContract = ethereumClient.getMutableContract(this.contract)
  }

  async createNFT(uri) {
    let nft = (await this.mutableContract.createNFT(uri)).logs[0].args.tokenId;
    return nft;
  }

  async deployAsset(
    deployer,
    oracle,
    nft,
    shares,
    symbol
  ) {
    let tx = await this.mutableContract
      .deployAsset(
        deployer,
        oracle,
        nft,
        shares,
        symbol
      )
    return (await tx.wait()).status
  }

  async safeTransferFrom(
    from,
    to,
    amount
  ) {
    let tx = await this.mutableContract
      .safeTransferFrom(
        from,
        to,
        amount
      )
    return (await tx.wait()).status
  }

}

export default PlatformContract
