import ServiceProvider from '../services/provider'
import WalletState from '../models/walletState'
import SubmissionsFirebase from '../utils/firebase' 
import { createFragmentMap } from '@apollo/client/utilities'

const wallet = ServiceProvider.wallet()
const platform = ServiceProvider.platform()

function state() {
  return {
    user: {
      wallet: WalletState
    },
    submissions: [],
    interface: {
      alert: null
    }
  }
}

const getters = {
  userWalletAddress(state) {
    return state.user.wallet.address
  },

  userEthBalance(state) {
    return state.user.wallet.ethBalance
  },

  submissions(state) {
    return state.submissions
  },

  submissionByIndex(state) {
    var submissionMap = new Map()

    state.submissions.forEach((submission, idx) => { 
        submissionMap.set(idx, submission)
      })

    return submissionMap
  },

  activeAlert(state) {
    return state.interface.alert
  }
}

const actions = {
  async syncWallet(context) {
    const walletState = await wallet.getState()
    context.commit('setWallet', walletState)
  },

  async syncSubmissions(context) {
    SubmissionsFirebase.get().on("value", (value) => {
      const submissions_url = "https://ipfs.io/ipfs/" + value.val().iphash 
      fetch(submissions_url)
        .then(response => response.json())
        .then(data => {
          const submissions = data
            context.commit('setSubmissions', submissions)
        });
    })
  },

  async tokenize(context, uri) {
    let res = await platform.tokenize(uri)
    console.log('---------', res)
  },

  dismissAlert(context) {
    context.commit('setAlert', null)
  }
}

const mutations = {
  setWallet(state, wallet) {
    state.user.wallet = wallet
  },

  setEthBalance(state, ethBalance) {
    state.user.wallet.ethBalance = ethBalance
  },

  setSubmissions(state, submissions) {
    state.submissions = submissions
  },

  removeSubmissionByIndex(state, index) {
    const _submissions = state.submissions.concat()
    _submissions.splice(index, 1)
    state.submissions = _submissions

    let old_hash = ''
    SubmissionsFirebase.get().on("value", (value) => {
      old_hash = value.val().iphash
    })

    let requestOptions = {
      method: "POST",
      headers: { 
        "Content-Type": "application/json",
        "pinata_api_key": process.env.VUE_APP_PINATA_API_KEY,
        "pinata_secret_api_key": process.env.VUE_APP_PINATA_SECRET_API_KEY
      },
      body: JSON.stringify(_submissions)
    };
    fetch("https://api.pinata.cloud/pinning/pinJSONToIPFS", requestOptions)
      .then(response => response.json())
      .then(data => {
        const IpfsHash = data.IpfsHash
        SubmissionsFirebase.update(IpfsHash)

        // requestOptions.method = "DELETE"
        // requestOptions.body = null
        // fetch(`https://api.pinata.cloud/pinning/unpin/${old_hash}`, requestOptions)

      })
      .catch(error => console.log('error', error))
  },

  setAlert(state, alert) {
    state.interface.alert = alert
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}